import { useEffect ,useState} from 'react';
// import { paramCase } from 'change-case';
import { useParams, useLocation } from 'react-router-dom';
// @mui
import { Container } from '@mui/material';
// redux
// import {   useSelector } from '../../redux/store';
import { getCompanysId } from '../../redux/slices/company';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import ProductNewForm from '../../sections/@dashboard/company/CompanyNewForm';

// ----------------------------------------------------------------------

export default function CompanyCreate() {
  const { themeStretch } = useSettings();
  // const dispatch = useDispatch();
  const { pathname } = useLocation();
  const { name } = useParams();
  // const { products } = useSelector((state) => state.product);
  const isEdit = pathname.includes('edit');
  const [currentProduct,setcurrentProduct] =useState({})
  // const currentProduct = products ? products.find((product) => paramCase(product.name) === name) : undefined;
  // const currentProduct =   products.find((product) => paramCase(product.name) === name);
 
  useEffect(() => {
    async function fetchData() {
    if(name){
      const value = await getCompanysId(name)
      setcurrentProduct(value)
    }
    // dispatch(getProductsId())
  }
  fetchData();;
  }, []);

  return (
    <Page title="Comapany: Create a new Company">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Create a new Comapny' : 'Edit Company'}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            {
              name: 'Company',
              href: PATH_DASHBOARD.company.list,
            },
            { name: !isEdit ? 'New ' : `${currentProduct.name}` },
          ]}
        />

        <ProductNewForm isEdit={isEdit} currentProduct={currentProduct} />
      </Container>
    </Page>
  );
}
