import { createSlice } from '@reduxjs/toolkit';
import sum from 'lodash/sum';
import uniqBy from 'lodash/uniqBy';
// utils
// import axios from '../../utils/axios';
import axios from 'axios';
//
import { dispatch } from '../store';
import {HOST_API} from '../../config'

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  companys: [],
  company: null,
  sortBy: null,
  filters: {
    gender: [],
    category: 'All',
    colors: [],
    priceRange: '',
    rating: '',
  },
  checkout: {
    activeStep: 0,
    cart: [],
    subtotal: 0,
    total: 0,
    discount: 0,
    shipping: 0,
    billing: null,
  },
};

const slice = createSlice({
  name: 'company',
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET companyS
    getcompanysSuccess(state, action) {
      state.isLoading = false;
      state.companys = action.payload;
    },

    // GET company
    getcompanySuccess(state, action) {
      state.isLoading = false;
      state.company = action.payload;
    },

    //  SORT & FILTER companyS
    sortBycompanys(state, action) {
      state.sortBy = action.payload;
    },

    filtercompanys(state, action) {
      state.filters.gender = action.payload.gender;
      state.filters.category = action.payload.category;
      state.filters.colors = action.payload.colors;
      state.filters.priceRange = action.payload.priceRange;
      state.filters.rating = action.payload.rating;
    },

    // CHECKOUT
    getCart(state, action) {
      const cart = action.payload;

      const subtotal = sum(cart.map((cartItem) => cartItem.price * cartItem.quantity));
      const discount = cart.length === 0 ? 0 : state.checkout.discount;
      const shipping = cart.length === 0 ? 0 : state.checkout.shipping;
      const billing = cart.length === 0 ? null : state.checkout.billing;

      state.checkout.cart = cart;
      state.checkout.discount = discount;
      state.checkout.shipping = shipping;
      state.checkout.billing = billing;
      state.checkout.subtotal = subtotal;
      state.checkout.total = subtotal - discount;
    },

    addCart(state, action) {
      const company = action.payload;
      const isEmptyCart = state.checkout.cart.length === 0;

      if (isEmptyCart) {
        state.checkout.cart = [...state.checkout.cart, company];
      } else {
        state.checkout.cart = state.checkout.cart.map((_company) => {
          const isExisted = _company.id === company.id;
          if (isExisted) {
            return {
              ..._company,
              quantity: _company.quantity + 1,
            };
          }
          return _company;
        });
      }
      state.checkout.cart = uniqBy([...state.checkout.cart, company], 'id');
    },

    deleteCart(state, action) {
      const updateCart = state.checkout.cart.filter((item) => item.id !== action.payload);

      state.checkout.cart = updateCart;
    },

    resetCart(state) {
      state.checkout.activeStep = 0;
      state.checkout.cart = [];
      state.checkout.total = 0;
      state.checkout.subtotal = 0;
      state.checkout.discount = 0;
      state.checkout.shipping = 0;
      state.checkout.billing = null;
    },

    onBackStep(state) {
      state.checkout.activeStep -= 1;
    },

    onNextStep(state) {
      state.checkout.activeStep += 1;
    },

    onGotoStep(state, action) {
      const goToStep = action.payload;
      state.checkout.activeStep = goToStep;
    },

    increaseQuantity(state, action) {
      const companyId = action.payload;
      const updateCart = state.checkout.cart.map((company) => {
        if (company.id === companyId) {
          return {
            ...company,
            quantity: company.quantity + 1,
          };
        }
        return company;
      });

      state.checkout.cart = updateCart;
    },

    decreaseQuantity(state, action) {
      const companyId = action.payload;
      const updateCart = state.checkout.cart.map((company) => {
        if (company.id === companyId) {
          return {
            ...company,
            quantity: company.quantity - 1,
          };
        }
        return company;
      });

      state.checkout.cart = updateCart;
    },

    createBilling(state, action) {
      state.checkout.billing = action.payload;
    },

    applyDiscount(state, action) {
      const discount = action.payload;
      state.checkout.discount = discount;
      state.checkout.total = state.checkout.subtotal - discount;
    },

    applyShipping(state, action) {
      const shipping = action.payload;
      state.checkout.shipping = shipping;
      state.checkout.total = state.checkout.subtotal - state.checkout.discount + shipping;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const {
  getCart,
  addCart,
  resetCart,
  onGotoStep,
  onBackStep,
  onNextStep,
  deleteCart,
  createBilling,
  applyShipping,
  applyDiscount,
  increaseQuantity,
  decreaseQuantity,
  sortBycompanys,
  filtercompanys,
} = slice.actions;

