export async function getcompany (skip,limit,search,from,to) {
  try {  
    const response = await axios.post(`${HOST_API}dealers/list`,{'skip':skip,'limit':limit,'search':search,from,to});
   return response.data.response
    // if(response.data.status !==0){
    //   dispatch(slice.actions.getcompanysSuccess(response.data.response.data));
    // }else{
    //   dispatch(slice.actions.hasError(response.data.invoiceId));
    // }
  } catch (error) {
    dispatch(slice.actions.hasError(error));
  }
}

export async function getCompanysId (id) { 
    try { 
      const response =await axios.post(`${HOST_API}dealers/Details`,{_id:id});
      return response.data.response
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
}

export async function savecompany (_id,paidStatus) { 
  try { 
    const response =await axios.post(`${HOST_API}dealers/save`,{paidStatus,_id});
    return response.data.response
  } catch (error) {
    dispatch(slice.actions.hasError(error));
  }
}

