import moment from 'moment';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
import {HOST_API} from '../../../config'

// components
import {
  FormProvider,
  // RHFSwitch,
  RHFSelect,
  // RHFEditor,
  RHFTextField,
  RHFRadioGroup,
  // RHFUploadMultiFile,
  RHFDatePicker,
 
} from '../../../components/hook-form';

