const LabelStyle = styled(Typography)(({ theme }) => ({
  ...theme.typography.subtitle2,
  color: theme.palette.text.secondary,
  marginBottom: theme.spacing(1),
}));



// ----------------------------------------------------------------------

ProductNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentProduct: PropTypes.object,
};

export default function ProductNewForm({ isEdit, currentProduct }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const NewProductSchema = Yup.object().shape({
    // customerName: Yup.string().required('Customer Name is required'),
    // purchaseId: Yup.string().required('purchase Id is required'),
    // customerPhone: Yup.string().required('customer Phone is required'),
    // customerAddress: Yup.string().required('Customer Address is required'),
    // amountTwo: Yup.string().required('Amount is required'),
    // amountOne: Yup.string().required('Amount is required'),
    // description: Yup.string().required('Description is required'),
    // images: Yup.array().min(1, 'Images is required'),
    // price: Yup.number().moreThan(0, 'Price should not be €0.00'),
  });

  const defaultValues = useMemo(
    () => ({
      _id: currentProduct?._id || '',
      name: currentProduct?.name || '',
      phone: currentProduct?.phone || '',
      address: currentProduct?.address || '',
      tin: currentProduct?.tin || '',
      postal: currentProduct?.postal || '',
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentProduct]
  );

  const methods = useForm({
    resolver: yupResolver(NewProductSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    setValue,
    getValues,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  const values = watch();

  useEffect(() => {
    if (isEdit && currentProduct) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentProduct]);

  const getTotalCosts = () => {
    return values.totalOne + values.totalTwo;
  };

  const onSubmit = async () => {
    console.log("try", values);
     
    if (values._id === "") { delete values._id }
      try {
        await new Promise((resolve) => setTimeout(resolve, 500));
        await axios.post(`${HOST_API}dealers/save`, values)
          .then((response) => {
            console.log(response);
            if (response.data.status === 1) {
              reset();
              enqueueSnackbar(!isEdit ? 'Create success!' : 'Update success!');
              navigate(PATH_DASHBOARD.company.list);
            } else {
              enqueueSnackbar('Company already exist', { variant: "error" });
            }
          }, (error) => {
            console.log(error);
          });
      } catch (error) {
        console.error(error);
      }
  };

  const getCompany = async (phone) => {
    console.log(phone);
    // console.log(getCompanyId(phone));
    const response = axios.post(`${HOST_API}dealers/Details`, { 'phone': phone })
      .then((response) => {
        if (phone === response.data.phone) {
          enqueueSnackbar('Phone is already exist', { variant: "error" });
          setValue('phone', '')
        }
      })
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>

          <Card sx={{ p: 4 }}>
            <LabelStyle>Purchase Details</LabelStyle>
            <Stack spacing={3}>
              <Box
                sx={{
                  display: 'grid',
                  columnGap: 2,
                  rowGap: 3,
                  gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
                }}
              >
                <RHFTextField name="name" label="Name" />
                <RHFTextField name="phone" label="Phone" onBlur={(event) => {
                  getCompany(getValues('phone'))
                }
                } />
                <RHFTextField name="address" label="Address" />
                <RHFTextField name="tin" label="TIN" />
                <RHFTextField name="postal" label="PostalCode" />

             
 

              </Box>

              <LabelStyle>Action</LabelStyle>

              <Box sx={{
                // display: 'grid',
                alignSelf: 'right',
                // columnGap: 2,
                // rowGap: 3,
                // gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(3, 1fr)' },
              }} >

                <LoadingButton type="submit" variant="contained" size="large" loading={isSubmitting}>
                  {!isEdit ? 'Create ' : 'Save Changes'}
                </LoadingButton>
              </Box>

            </Stack>
          </Card>
        </Grid>

      </Grid>
    </FormProvider>
  );
}
